html{
  height: 100vh;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
  height: 100%;
  width: 100%;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  width: 100%;
  height: 100%;
  background-color: #0B2434;
  display: flex;
  justify-content: center;
  align-items: center;
}
.panel{
  background-color: #F5F5F5;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  width: 80%;
  flex-wrap: wrap;
  padding: 2em;
  border-radius: 0.5em;
  margin: 0;
}
.App{
  width: 60%;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2em;
}
.dice{
  box-shadow: 2px 4px 7px rgba(0,0,0,0.6);
  border-radius: 0.2em;
  width: 15%;
  aspect-ratio: 1/1 !important;
  cursor: pointer;
}
.hold-dice{
  background-color: #59E391;
  box-shadow: 2px 4px 7px rgba(0,0,0,0.6);
  border-radius: 0.2em;
  width: 15%;
  aspect-ratio: 1/1 !important;
  cursor: pointer;
}
.title{
  font-size: 4em;
  color: #f5f5f5;
  margin: 0;
}
.App p{
  font-size: 1em;
  color: #f5f5f5;
}
.btn{
  width: 95%;
  box-shadow: 3px 6px 7px rgba(0,0,0,0.6);
  background-color: #59E391;
  color: #f5f5f5;
  border-style: none;
  padding: 0.5em;
  border-radius: 0.2em;
  cursor: pointer;
  font-size: 1em;
}

.btn:focus {
  outline: none;
}

.btn:active {
  box-shadow: inset 2px 2px 10px -3px rgba(0, 0, 0, 0.4);
  text-shadow: 1px 1px 1px rgba(0,0,0, 0.4);
}
@media screen and (max-width: 630px){
  .dice{
    width: 25%;
  }
}
